import { signOut } from 'firebase/auth';
import { useAuthState } from "react-firebase-hooks/auth";
import toast from 'react-hot-toast';
import { Link, useNavigate } from "react-router-dom";
import auth from "../../../firebase.init";

export default function Navbar() {
    const [user] = useAuthState(auth)

    // console.log(user);

    const navigate = useNavigate();
    const logout = () => {
        signOut(auth);
        toast.success(`Thank you, ${user?.displayName} to stay with us!`, {
            position: "bottom-right",
            autoClose: 5000,
        });
        localStorage.removeItem("accessToken");
        navigate("/");
    };
    const Navmenu = (
        <li>
            <Link to="/">Home</Link>
            <Link to="/appointment">Appointment</Link>
            <Link to="/review">Review</Link>
            <Link to="/contact">Contact</Link>
            <Link to="/about">About</Link>
            {
                user && <Link to="/dashboard">Dashboard</Link>
            }
            

        </li>
    )
    return (
        <div className="navbar bg-base-100">
            <div className="navbar-start">
                <div className="dropdown">
                    <label tabIndex="0" className="btn btn-ghost lg:hidden">
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h8m-8 6h16" /></svg>
                    </label>
                    <ul tabIndex="0" className="menu menu-compact dropdown-content mt-3 p-2 shadow bg-base-100 rounded-box w-52">
                        {Navmenu}
                    </ul>
                </div>
                <Link to="/" className="btn btn-ghost normal-case text-xl">Doctors Portal</Link>
            </div>
            <div className="navbar-center hidden lg:flex">
                <ul className="menu menu-horizontal p-0">
                    {Navmenu}
                </ul>
            </div>
            <div className="navbar-end">
                {
                    user ? (
                        <div className="dropdown dropdown-end">
                            <label tabIndex="0" className="btn btn-ghost btn-circle avatar">
                                <div className="w-10 rounded-full">
                                    <img src="https://placeimg.com/80/80/people" alt={user?.displayName} />
                                </div>
                            </label>
                            <ul tabIndex="0" className="menu menu-compact dropdown-content mt-3 p-2 shadow bg-base-100 rounded-box w-52">
                                <li>
                                    <Link to="/profile" className="justify-between mb-2">
                                        Profile
                                        <span className="badge">New</span>
                                    </Link>
                                </li>
                                {
                                    user && <label htmlFor="dashboard-sidebar" className="btn drawer-button lg:hidden mb-2">Open drawer</label>
                                }
                                {user && <button className="btn btn-primary font-bold" onClick={logout}>Logout</button>}
                            </ul>
                        </div>
                    ) : (
                        <Link to="/login" className="btn btn-ghost">Login</Link>
                    )
                }
            </div>
        </div>
    )
}
